import React, { Component } from "react";
import img from "../data/GitHub-Mark-Light-64px.png";

const Footer = () => {
  <a href="https://github.com/nicholaspun">
    <img src={img} alt="github logo" className="logo" />
  </a>
}

export default Footer;



